#sponsers{
    width: 100% ;
    height: auto;
    overflow: hidden;
    position: relative;
    padding: 0em 0 1em 0 ;
    overflow-x : hidden;
}
#sponsers h4{
    display: inline-block ;
    width: auto ;
    font-size: 2em ;
    font-weight: 700 ;
    color: rgb(40,40,40) ;
    border-bottom: .8px solid #80808070 ;
    padding-bottom: 1em ;
    margin-right: 1em;
}
#sponsers .my-slider{
    width: auto ;
    height: auto;
    display: flex ;
    flex-direction: row ;
    justify-content: flex-start ;
    align-items: center ;
    flex-wrap: nowrap ;
    margin-top: 2em ;
    position: relative ;
    top: 0 ;
    right: 0 ;
}
#sponsers .my-slider .my-slide{
    text-decoration: none ;
    flex-shrink: 0 ;
    width: 12em ;
    height: 12em;
    margin: 0 1em ;
    box-shadow: 0 0 .5em .01em rgba(60,60,60,.3) ;
    position: relative ;
    border-radius: 1.5em ;
}
#sponsers .my-slide img{
    width: 100% ;
    height: 65%;
    background-position: center center ;
    background-repeat: no-repeat ;
    background-size: contain ;
}
#sponsers .my-slide p{
    width: 100% ;
    height: auto ;
    text-align: center ;
    position: absolute;
    right: 50% ;
    transform: translateX(50%) ;
    bottom : 5% ;
    color: rgb(50,50,50) ;
    font-size: 1em ;
    font-weight: 600 ;
}
@media screen and (max-width:900px){
    #sponsers{font-size: 16px;}
}

@media screen and (max-width:550px){
    #sponsers{font-size: 12px ;}
}
@media screen and (max-width:320px){
    #sponsers h4{font-size: 1.5em ;}
}
